@import "../styles/variables";

.history {
    position: relative;
    margin: 0 400px;
    padding: 0 !important;
    &-image {
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
        z-index: -1;
        height: 100%;
    }
}

.timeline-item {
    animation: fadeInUp 0.5s ease-out forwards;
    color: $header-color;
    background-color: white;
}

@keyframes fadeInUp {
    from {
        background-color: white;
    }
    to {
        background-color: inherit;
    }
}
